import { GameID, PlayerID, PlayerShotBin, VideoID } from '@types';
import { wrapWorker } from 'common/@utils';
import { DataLoader } from 'common/DataLoader';
import IBallWorker from './loadData.worker'

export class IBallDataLoader extends DataLoader<GameID, VideoID, PlayerID> {

    #getBins: (playerIds: PlayerID[]) => Promise<Partial<Record<PlayerID, PlayerShotBin[]>>>

    constructor() {
        super(IBallWorker)
        this.#getBins = wrapWorker<[PlayerID[]], Partial<Record<PlayerID, PlayerShotBin[]>>>(this.worker, 'Bins')
    }

    /**
     * -------------- Game data ----------------
     */
    playerBins: Partial<Record<PlayerID, PlayerShotBin[]>> = {}
    async loadBins(playerIds: PlayerID[] = []) {
        this.playerBins = await this.#getBins(playerIds);
    }
}